.features {
    @include flexCenter(row, space-evenly);
    flex-wrap: wrap;
    max-width: 1200px;
    margin: 0 auto;

    #{&}-card {
        @include bordered(2px, $primary-color);
        width: 250px;
        margin: 10px;
        padding: 10px;
        background-color: #c8d8e4;
        transition: box-shadow 0.3s;
        border-radius: 10px;
        cursor: pointer;

        &:hover {
            box-shadow: 0 2px 6px $primary-color;
        }
    }

    #{&}-head {
        @include flexCenter(row, center);
        font-size: 20px;
        margin-bottom: 10px;
        text-align: center;
        color: $primary-color;
        font-weight: 500;
    }

    #{&}-para {
        text-align: center;
    }
}